.notify {
		background-color: $gray-bg;
		padding: 60px 0;
		&__content {
				display: flex;
				justify-content: space-between; }
		&__left {
			max-width: 495px; }
		&__title {
			@include ralewaysemibold;
			margin-bottom: 25px; }
		&__text {
			@include helveticareg;
			line-height: 26px;
			margin-bottom: 30px; }
		&__video {
			width: 500px;
			height: 307px;
			iframe {
				width: 100%;
				height: 100%; } } }
.callback {
	display: flex;
	&__input {
		border: 1px solid #c8c8c8;
		max-width: 305px;
		border-top-left-radius: 15px;
		border-bottom-left-radius: 15px;
		border-right: none;
		width: 305px;
		height: 56px;
		padding-left: 22px;
		@include helveticareg;
		font-size: 18px; }
	&__btn {
		width: 140px;
		height: 56px;
		border: 1px solid $green;
		max-width: 140px;
		background-color: $green;
		border-top-right-radius: 15px;
		border-bottom-right-radius: 15px;
		border-left: none;
		@include ralewaybold;
		font-size: 22px;
		color: $white;
		&:hover {
			background-color: lighten($green, 10%);
			border-color: lighten($green, 10%); } } }
