.features {
	background-color: $white;
	padding: 60px 0;
	&__content {
		display: flex;
		justify-content: space-between; }
	&__block {
		max-width: 325px;
		text-align: center; } }
.feature {
	&__img {
		width: 98px;
		height: 98px;
		background-color: $green;
		border-radius: 50%;
		margin: 0 auto 30px;
		p {
		 font-size: 48px;
		 line-height: 110px; } }
	&__title {
		@include ralewaysemibold;
		margin-bottom: 25px; }
	&__text {
		p {
			@include helveticareg;
			line-height: 26px; } } }
