.wrap {
  max-width: 1200px;
  margin: 0 auto;
}

.container {
  margin: 0 10px;
}

.h1 {
  font-family: 'pacificoreg', sans-serif;
  font-size: 60px;
  color: #fff;
}

.header__topline {
  height: 10px;
  background: #2ecc71;
}

.header__bg {
  background: url("../img/header/bg.jpg") center center no-repeat;
  background-size: cover;
  padding: 167px 0px 176px;
}

.header__content {
  margin-left: 120px;
}

.header__caption {
  margin-bottom: 25px;
}

.header__title {
  margin-bottom: 35px;
}

.header__title span {
  color: #2ecc71;
}

.header__subtitle {
  font-family: 'helveticamed', sans-serif;
  font-size: 24px;
  color: #fff;
  color: #fff;
  line-height: 36px;
  max-width: 453px;
}

.os__list {
  display: flex;
  justify-content: space-between;
  max-width: 190px;
}

.os__item {
  width: 55px;
  height: 55px;
  background-color: #000;
  border-radius: 50%;
  opacity: 0.6;
  text-align: center;
}

.os__item:hover {
  background-color: #2ecc71;
  opacity: 1;
}

.os__link {
  line-height: 61px;
  font-size: 25px;
  display: block;
  opacity: 1 !important;
}

.os__link:hover .icon {
  fill: #fff;
}

.features {
  background-color: #fff;
  padding: 60px 0;
}

.features__content {
  display: flex;
  justify-content: space-between;
}

.features__block {
  max-width: 325px;
  text-align: center;
}

.feature__img {
  width: 98px;
  height: 98px;
  background-color: #2ecc71;
  border-radius: 50%;
  margin: 0 auto 30px;
}

.feature__img p {
  font-size: 48px;
  line-height: 110px;
}

.feature__title {
  font-family: 'ralewaysemibold', sans-serif;
  font-size: 22px;
  color: #222222;
  margin-bottom: 25px;
}

.feature__text p {
  font-family: 'helveticareg', sans-serif;
  font-size: 16px;
  color: #848484;
  line-height: 26px;
}

.notify {
  background-color: #e3e3e3;
  padding: 60px 0;
}

.notify__content {
  display: flex;
  justify-content: space-between;
}

.notify__left {
  max-width: 495px;
}

.notify__title {
  font-family: 'ralewaysemibold', sans-serif;
  font-size: 22px;
  color: #222222;
  margin-bottom: 25px;
}

.notify__text {
  font-family: 'helveticareg', sans-serif;
  font-size: 16px;
  color: #848484;
  line-height: 26px;
  margin-bottom: 30px;
}

.notify__video {
  width: 500px;
  height: 307px;
}

.notify__video iframe {
  width: 100%;
  height: 100%;
}

.callback {
  display: flex;
}

.callback__input {
  border: 1px solid #c8c8c8;
  max-width: 305px;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  border-right: none;
  width: 305px;
  height: 56px;
  padding-left: 22px;
  font-family: 'helveticareg', sans-serif;
  font-size: 16px;
  color: #848484;
  font-size: 18px;
}

.callback__btn {
  width: 140px;
  height: 56px;
  border: 1px solid #2ecc71;
  max-width: 140px;
  background-color: #2ecc71;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  border-left: none;
  font-family: 'ralewaybold', sans-serif;
  font-size: 18px;
  color: #2e2e2e;
  font-size: 22px;
  color: #fff;
}

.callback__btn:hover {
  background-color: #54d98c;
  border-color: #54d98c;
}

.contacts {
  padding: 60px 0;
}

.contacts__blockquote {
  max-width: 825px;
  margin: 0 auto 25px;
}

.contacts__in {
  max-width: 500px;
  margin: 0 auto;
  text-align: center;
}

.contacts__slogan {
  font-family: 'ralewaysemibold', sans-serif;
  font-size: 22px;
  color: #222222;
  font-size: 24px;
  margin-bottom: 20px;
}

.contacts__text {
  font-family: 'helveticareg', sans-serif;
  font-size: 16px;
  color: #848484;
  margin-bottom: 25px;
}

.contacts__blockquote_text {
  font-family: 'helveticaitalic', sans-serif;
  font-size: 18px;
  color: #848484;
  line-height: 30px;
  margin-bottom: 25px;
  color: #848484;
}

.contacts__blockquote_author {
  font-family: 'ralewaybold', sans-serif;
  font-size: 18px;
  color: #2e2e2e;
  text-align: center;
  margin-bottom: 30px;
}

.contacts__face {
  margin-bottom: 60px;
}

.face {
  display: flex;
  max-width: 1140px;
  justify-content: space-between;
}

.face__block {
  width: 50px;
  height: 50px;
}

.face__wrap {
  width: 100%;
  height: 100%;
  background-color: transparent;
}

.face__wrap:hover {
  background-color: #2ecc71;
  opacity: 0.6;
  cursor: pointer;
}

.socials__list {
  display: flex;
  justify-content: space-between;
  max-width: 255px;
  margin: 0 auto;
}

.socials__item {
  width: 34px;
  height: 34px;
  background-color: #c5c5c5;
  border-radius: 50%;
}

.socials__item:hover {
  background-color: #2ecc71;
}

.socials__link {
  display: block;
  font-size: 18px;
  line-height: 40px;
}

.footer {
  padding: 60px 0;
  background-color: #e3e3e3;
}

.footer__content {
  max-width: 605px;
  margin: 0 auto;
}

.nav {
  display: flex;
  justify-content: space-between;
}

.nav__link {
  font-family: 'helveticareg', sans-serif;
  font-size: 16px;
  color: #848484;
  padding: 5px;
  text-decoration: none;
}

.nav__link:hover {
  color: #5e5e5e;
}
