.header {
	&__topline {
		height: 10px;
		background: $green; }
	&__bg {
		background: url("../img/header/bg.jpg") center center no-repeat;
		background-size: cover;
		padding: 167px 0px 176px; }
	&__content {
		margin-left: 120px; }
	&__caption {
		margin-bottom: 25px; }
	&__title {
		margin-bottom: 35px;
		span {
			color: $green; } }
	&__subtitle {
		@include helveticamed;
		color: $white;
		line-height: 36px;
		max-width: 453px; } }
.os {
		&__list {
			display: flex;
			justify-content: space-between;
			max-width: 190px; }
		&__item {
			width: 55px;
			height: 55px;
			background-color: $dark;
			border-radius: 50%;
			opacity: 0.6;
			text-align: center;
			&:hover {
				background-color: $green;
				opacity: 1; } }
		&__link {
			line-height: 61px;
			font-size: 25px;
			display: block;
			opacity: 1 !important;
			&:hover {
				.icon {
					fill: $white; } } } }
