@font-face {
	font-family: 'pacificoreg';
	src: url('../fonts/Pacifico-Regular.woff2') format('woff2'), url('../fonts/Pacifico-Regular.woff') format('woff');
	font-weight: normal;
	font-style: normal; }

@font-face {
	font-family: 'ralewaybold';
	src: url('../fonts/Raleway-Bold.woff2') format('woff2'), url('../fonts/Raleway-Bold.woff') format('woff');
	font-weight: bold;
	font-style: normal; }

@font-face {
	font-family: 'ralewaysemibold';
	src: url('../fonts/Raleway-SemiBold.woff2') format('woff2'), url('../fonts/Raleway-SemiBold.woff') format('woff');
	font-weight: 600;
	font-style: normal; }

@font-face {
	font-family: 'helveticabolditalic';
	src: url('../fonts/HelveticaNeueCyr-BoldItalic.woff2') format('woff2'), url('../fonts/HelveticaNeueCyr-BoldItalic.woff') format('woff');
	font-weight: bold;
	font-style: italic; }

@font-face {
	font-family: 'helveticaitalic';
	src: url('../fonts/HelveticaNeueCyr-Italic.woff2') format('woff2'), url('../fonts/HelveticaNeueCyr-Italic.woff') format('woff');
	font-weight: normal;
	font-style: italic; }

@font-face {
	font-family: 'helveticamed';
	src: url('../fonts/HelveticaNeueCyr-Medium.woff2') format('woff2'), url('../fonts/HelveticaNeueCyr-Medium.woff') format('woff');
	font-weight: 500;
	font-style: normal; }

@font-face {
	font-family: 'helveticareg';
	src: url('../fonts/HelveticaNeueCyr-Roman.woff2') format('woff2'), url('../fonts/HelveticaNeueCyr-Roman.woff') format('woff');
	font-weight: normal;
	font-style: normal; }

@mixin pacificoreg {
	font-family: 'pacificoreg', sans-serif;
	font-size: 60px;
	color: $white; }
@mixin ralewaybold {
	font-family: 'ralewaybold', sans-serif;
	font-size: 18px;
	color: $text-author; }
@mixin ralewaysemibold {
	font-family: 'ralewaysemibold', sans-serif;
	font-size: 22px;
	color: $text-bold; }
@mixin helveticabolditalic {
	font-family: 'helveticabolditalic', sans-serif;
	font-size: 18px;
	color: $text; }
@mixin helveticaitalic {
	font-family: 'helveticaitalic', sans-serif;
	font-size: 18px;
	color: $text; }
@mixin helveticamed {
	font-family: 'helveticamed', sans-serif;
	font-size: 24px;
	color: $white; }
@mixin helveticareg {
	font-family: 'helveticareg', sans-serif;
	font-size: 16px;
	color: $text; }

