.contacts {
	padding: 60px 0;
	&__blockquote {
		max-width: 825px;
		margin: 0 auto 25px; }
	&__in {
		max-width: 500px;
		margin: 0 auto;
		text-align: center; }
	&__slogan {
		@include ralewaysemibold;
		font-size: 24px;
		margin-bottom: 20px; }
	&__text {
		@include helveticareg;
		margin-bottom: 25px; }
	&__blockquote {
		&_text {
			@include helveticaitalic;
			line-height: 30px;
			margin-bottom: 25px;
			color: $text; }
		&_author {
			@include ralewaybold;
			text-align: center;
			margin-bottom: 30px; } }
	&__face {
		margin-bottom: 60px; } }
.face {
	display: flex;
	max-width: 1140px;
	justify-content: space-between;
	&__block {
		width: 50px;
		height: 50px; }
	&__wrap {
		width: 100%;
		height: 100%;
		background-color: transparent;
		&:hover {
			background-color: $green;
			opacity: 0.6;
			cursor: pointer; } } }
.socials {
	&__list {
		display: flex;
		justify-content: space-between;
		max-width: 255px;
		margin: 0 auto; }
	&__item {
		width: 34px;
		height: 34px;
		background-color: #c5c5c5;
		border-radius: 50%;
		&:hover {
			background-color: $green; } }
	&__link {
		display: block;
		font-size: 18px;
		line-height: 40px; } }
